<template>
 
    <div class="bottom-item">
         <v-gouwu :showgouwuD="showgouwuD" :selectcount="selectcount"></v-gouwu>
    <div class="bott1">
      <div class="gouwu" @click="showgouwu" :class="{shgouwu:totalPrice>0}">
        <i class="iconfont icon-gouwuche"></i>
      </div>
      <div class="foodnums" v-show="foodsnum>0"><span class="minn">{{foodsnum}}</span></div>
     <p class="beisong" :class="{shbeisong:totalPrice>0}">¥{{caleSong}}</p>
    </div>
    <div class="bott2" >
      <span>另需配送费用￥{{this.seller.deliveryPrice}}元</span>
       <span class="cale" :class="{shcale:totalPrice>=seller.minPrice}">{{songMoney}}</span>
    </div>
  </div>

</template>

<script>
import { mapGetters } from 'vuex';
import gouwuchedetil from '../gouwuchedetil/gouwuchedetil.vue';
export default {
 
 data(){
    return{
      showgouwuD:false
      
    }
  },
  components:{
    'v-gouwu': gouwuchedetil
  },
  computed:{
    ...mapGetters([
      'seller',
      'goods'
    ]),
    selectcount(){
      let foodDetil=[];
      this.goods.forEach(foodlist=>{
        foodlist.foods.forEach(
          food=>{
            if(food.count>0){
              foodDetil.push(food)
            }
          }
        )
      });
      return  foodDetil;
    },
    foodsnum(){
      let allcount=0;
      this.selectcount.forEach(food=>{
        allcount+=food.count
      }) 
      return allcount;
    },
    totalPrice(){
      let price=0;
      this. selectcount.forEach(food=>{
        price+=food.price*food.count;
      });
     
      return price;
    },
    songMoney(){
     if(this.totalPrice==0){
        return `¥${this.seller.minPrice}元起送`;
       
     }else if(this.totalPrice<this.seller.minPrice){
       return `还差¥${this.seller.minPrice-this.totalPrice}元起送`;
     }
     return "去结算";
    },
    caleSong(){
      if(this.totalPrice==0){
        return 0;
      }
     return this.seller.deliveryPrice + this.totalPrice
    }
  },
  methods:{
    showgouwu(){
      this.showgouwuD=!this.showgouwuD;
    }
  }
  
  
}
</script>

<style lang="less" scoped>

    .bottom-item{
    position:fixed;
    bottom:0rem;
    width:100%;
    height:0.95rem;
    line-height:0.95rem;
    background-color:#141d27;
   z-index:9;
    .bott1{
      border-right:0.01rem solid #2b343c;
      width:2.6rem;
     .beisong{
        color:#80858a;
        font-size:0.32rem;
        font-weight: 500;
        padding-left: 1.8rem;
        &.shbeisong{
          color:rgb(255,255,255);
        }
      }
      .foodnums{
        font-size:0.28rem;
        font-weight:700;
        color:rgb(255,255,255);
        position: absolute;
        bottom:1rem;
        left:1rem;
        width:0.5rem;
        height: 0.5rem;
        background-color:red;
        border-radius:0.2rem;
        span{
          display: block;
          position: relative;
          left: 0.12rem;
          top:-0.2rem;
          font-weight:800;
        }
      }
      .gouwu{
        position: absolute;
        bottom:0.1rem;
        left:0.36rem;
        width:0.88rem;
        height: 0.88rem;
        line-height: 0.88rem;
        border:0.15rem solid #141d27;
        border-radius:0.88rem;
        background-color:#2b343c;
        color:#80858a;
        text-align: center;
        .iconfont{
          font-size:0.6rem;    
        }
        &.shgouwu{
        background-color:rgb(0,160,220);
        color:rgb(255,255,255);
        }
        }
    }
    .bott2{
      position:absolute;
      left:3rem;
      top:0rem;
      color:rgba(255,255,255,0.4);
      font-weight:100;
      font-size:0.24rem;
      width:4rem;
      &.shbott2{
        color:rgb(255,255,255);
      }
       
      .cale{
        color:#80858a;
        position:absolute;
        left:2.3rem;
        top:0.03rem;
        display:block;
        font-size:0.3rem;
        font-weight: 500;
        text-align:center;
        background-color:#2b343c;
        width:2.2rem;
        &.shcale{
        background-color:rgb(0,160,220);
        color:rgb(255,255,255);
        }
      }
    }
  }
</style>



