<template>
    <div class="gouwuche"  >
        <div @click.prevent="dec" class="dec" v-if="foods.count">
            <i class="icon iconfont icon-jianhao"></i>
        </div> 
        <span class="num" v-if="foods"><span v-if="foods.count">{{foods.count}}</span></span>
        <div @click="add" class="add">
            <i class="icon iconfont icon-jiahao"></i>
        </div>  
    </div>
</template>

<script>
import Vue from 'vue';
export default {
  props:[
    'foods'
  ],
 data:()=>{
     return{
        food:[] 
     }
 },
 methods:{
     add(){
         if(!this.foods.count){
             Vue.set(this.foods,'count',1);
             return;
         }
         this.foods.count++;
         
     },
     dec(){
        if(this.foods.count>0){
            this.foods.count--;
            return;
        }
        this.foods.count=0;
     }
 }

}
</script>

<style lang="less" scoped>
    .gouwuche{
        position: relative;
        left:1.5rem;
        bottom:0.4rem;
        width:2rem;
        display: flex;
        justify-content: flex-end;
        text-align:center;
        

    .dec{
        width:0.3rem;
        height:0.3rem;
        color:rgb(0,160,220);

    }
     .num{
        display:block;
        font-size:0.28rem;
        bottom:0.75rem;
        width:0.5rem;
        height:0.3rem;
       
    }
     .add{
        width:0.3rem;
        height:0.3rem;
        color:rgb(0,160,220);
    }         
    }
</style>
 


