<template>
  <div class="gouwudetail" v-show="showgouwuD">
    <div class="bd">
    </div>
    <div class="shangpdetil" ref="bd">
      <div>
      <div class="tip">
          <div class="qindan">购物车</div>
          <div class="qingk" @click="ee()">清空</div>
      </div>
      <div class="foodui">
      <div class="foodli" v-for="(item,i) in selectcount" :key="i">
        <div class="fname">{{item.name}}</div>
        <div class="sill"><span class="tt">¥ </span>{{item.price}}</div>
        <div class="addctr">
            <div @click.prevent="dec(item)"  class="dec">
                <i class="icon iconfont icon-jianhao"></i>
            </div> 
             <span class="num">{{item.count}}</span>
            <div @click="add(item)" class="add">
                 <i class="icon iconfont icon-jiahao"></i>
             </div>  
        </div>
      </div>
      </div>
      </div>
    </div>
   
  </div>
</template>

<script>

import { mapGetters } from 'vuex';
import BScroll from 'better-scroll';
import gouwuche from '../gouwuche/gouwuche.vue';
export default {
  props:[
    'showgouwuD',
    'selectcount'
  ],
  components:{
    'v-gouwuchw':gouwuche,
  },
  computed:{
    ...mapGetters([
        'foods'
    ])
  },
  created(){
    this.$nextTick(()=>{
      setTimeout(()=>{
        this. cscroll()
      })
    },300)
  },
  methods:{
      add(e){
        e.count++
      },
      dec(a){  
        a.count--
      },
      ee(){
      this.selectcount.forEach(rat=>{
        return rat.count=0;
      })
      },
       cscroll(){
     let croll= new BScroll(this.$refs.bd,{
      click:true
    })
  }
  },
 
  
}
</script>

<style lang="less" scoped>
  .gouwudetail{  
    .bd{
      position:fixed;
      top:0;
      left: 0;
      bottom:0rem;
      right: 0;
      filter:blur(0.1rem);
      background-color:rgba(7,17,27,0.6);
      }
    .shangpdetil{
      position:absolute;
      left: 0;
      bottom:0.9rem;;
      // height:6.1rem;
      right: 0;
      overflow: hidden;
      background-color:white;
      .tip{
          display: flex;
          justify-content: flex-end;
          height: 0.8rem;
          font-size:0.3rem;
          font-weight:700;
          color:rgba(7,17,27,0.5);
          border-bottom:1px solid rgba(7,17,27,0.1);
          background-color:#f3f5f7;
          
          .qingk{
              font-size:0.28rem;
              color:rgb(0,160,220);
              margin-right:0.36rem;
              margin-left:5.35rem;
          }
      }
      .foodui{
        overflow: hidden;
      .foodli{
          display: flex;
          justify-content:space-between;
          height: 0.96rem;
          margin-left:0.36rem;
          margin-right:0.4rem;
          border-bottom:0.01rem solid rgba(7,17,27,0.4);
          font-size:0.35rem;
          color:rgb(7,17,27);
     
          .addctr{
            width:2rem;
            text-align:center;
            display: flex;
            justify-content: flex-end;
            .dec{
            width:0.3rem;
            height:0.3rem;
            color:rgb(0,160,220);
            .icon{
                font-size:0.48rem;
            }
        }
            .num{
            display:block;
            color:rgb(147,153,159);
            font-size:0.28rem;
            bottom:0.75rem;
            width:0.6rem;
            padding-left:0.1rem;
            height:0.3rem;
       
        }
            .add{
            width:0.3rem;
            height:0.3rem;
            color:rgb(0,160,220);
             .icon{
                font-size:0.48rem;
            }
        }         
        }
            .sill{
            color:rgb(240,20,20);
            position: absolute;
            right:2.5rem;
            .tt{
                font-size:0.24rem;
                font-weight:100;  
            }
            }

      }
    }
    }
    
  }
</style>


