<template>
    <div class="star">
        <span v-for='staritem in staritems':class="staritem" :key="staritem" class="starall" ></span>
    </div>
</template>

<script>
const LENGTH=5;
const ON='on';
const HELF='helf';
const OFF='off';
export default {
props:[
    'score'   
    ],
computed:{
    staritems(){
    let result=[];
    let integer=Math.floor(this.score);
    let decimal=this.score-integer;
    for(let i=0;i<integer;i++){
        result.push(ON)
    };
    if( decimal>0.5){
        result.push(HELF)
    };
    while(result.length<LENGTH){
        result.push(OFF)
    };
    return result;
   
    },
    
    startype(){
    return 'star-' + 'this.size';
}
}
}

</script>

<style lang="less" scoped>
    .star{
        .starall{
            display: inline-block;
            width:0.24rem;
            height:0.24rem;
            margin-right:0.06rem;
            &.on{
                background-image: url(./star_on.png);
                background-size:0.24rem 0.24rem;
                background-repeat: no-repeat;
            }
            &.off{
                background-image: url(./star_off.png);
                background-size:0.24rem 0.24rem;
                background-repeat: no-repeat;
            }
            &.helf{
                background-image: url(./star_half.png);
                background-size:0.24rem 0.24rem;
                background-repeat: no-repeat;
            }
        }
       
    }
</style>


