<template>
  <div>
     	<div class="headertop">
        <div class="bg">
          <img :src="seller.avatar">
        </div>
         <img :src="seller.avatar" @click="showmoutop">
        <div class="bgt">
          <div class="fnna">
            <img src="../../../static/imgs/brand.png" class="fnimh1">
            <p class="fn1">{{seller.name}}</p>
            <p class="fn2">{{seller.description}}/{{seller.deliveryTime}}分钟送达</p>
            <img src="../../../static/imgs/decrease.png" class="fnimh2">
            <p class="fn3" v-if="seller.supports">{{seller.supports[0].description}}</p>
            <div class="raid"><span>5</span>个<span class="ttr">></span></div>
          </div>
        </div>
        <div class="tip0">
          <img src="../../../static/imgs/bulletin.png" class="gonggao">
          <p class="fff1">{{seller.bulletin}}</p><p class="huze">></p>
        </div>      
       </div>
  </div>
</template>

<script>
import { mapGetters , mapActions } from 'vuex';
export default {
  computed:{
      ...mapGetters([
          'seller'
      ])
  },
  methods:{
    ...mapActions([
      'showmoutop'
    ])
  }
 
  
}
</script>

<style lang="less" scoped>
.headertop{
 
  width:100%;
  height: 2.48rem;
  overflow: hidden;
  padding-top:0.48rem;
  padding-left:0.48rem;
  background-color:rgba(7,17,27,0.5);
  position:relative;
  .bg{
    position: absolute;
    top:0;
    left: 0;
    right: 0;
    bottom:0;
    z-index:-3;
    filter:blur(0.1rem);
    img{
      width:100%;
      height:100%;
    }
  }
   
img{
  width:1.28rem;
  width:1.28rem;
  border-radius:0.04rem;
  }
  .bgt{
    position: absolute;
    top:0.48rem;
    left:2.08rem;
    .fnna{
      padding:0;
      margin:0;
      .fnimh1{
        width:0.61rem;
        height:0.37rem;
      }
      .fnimh2{
        position: relative;
        top:0.2rem;
        width:0.29rem;
        height:0.29rem;
      }
      .fn1{
      position: relative;
      top:-0.4rem;
      left:0.69rem;
      font-size:0.32rem;
      color:rgb(255,255,255);
      font-weight:blod;
      line-height:0.36rem;
    }
      .fn2{
          position:absolute;
          top:0.53rem;
          font-size:0.24rem;
          font-weight:200;
          color:rgb(255,255,255);
          line-height:0.24rem;
         
      }
      .fn3{
        position: relative;
        top:-0.08rem;
        left: 0.34rem;
        font-size:0.24rem;
        color:rgb(255,255,255);
        font-weight:200;
        line-height:0.24rem;

      }
      .raid{
        position: absolute;
        bottom:0rem;
        left:4.1rem;
        width:0.80rem;
        height: 0.5rem;
        border-radius:0.2rem;
        font-size:0.24rem;
        color:rgb(255,255,255);
        font-weight:0.2;
        line-height:0.5rem;
        background-color:rgba(0,0,0,0.2);
        padding-left:0.16rem;
        .ttr{
          display:block;
          position:absolute;
          font-size:0.26rem;
          bottom:0.018rem;
          right:0.16rem;
        }
        span{
          margin-right:0.03rem;
        }
      }
  }

  }
  .tip0{
    position: absolute;
    left: -0.48rem;
    top:2.24rem;
    font-size:0.24rem;
    color:rgb(255,255,255);
    font-weight:200;
    width:100%;
    height:0.7rem;
    background-color:rgba(7,17,27,0.2);
    
    
    .gonggao{
      padding-top:0.25rem;
      padding-left:0.72rem;
      width:0.44rem;
      height:0.25rem;
    }
    .fff1{
      position: absolute;
      top:0.26rem;
      left:1.25rem;
      width:6.1rem;
      height: 0.32rem;
      overflow: hidden;
      text-overflow:ellipsis;
      white-space: nowrap;
    }
    .huze{
      position: absolute;
      font-size:0.26rem;
      top:0.24rem;
      left:7.6rem;
    }
  }
  
  
  
  
  
 
 
  
  
}
</style>




