<template>
  <div class="foodinfo"  v-show="showfood" ref="foodpag">
    <div class="bd">
      <div class="foodimg">
        <img :src="foods.image" @click="fanhui">
      </div>

        <div class="item-one">
          <p class="fn1">{{foods.name}}</p>
          <div class="fn2">
            <span> 月售{{foods.sellCount}}份</span>
            <span> 好评率{{foods.rating}}％</span>
          </div>
          <p class="fn3"><span class="fft">¥</span>{{foods.price}}</p>
          <div class="addce" @click="addcar(foods)" v-show="!foods.count">加入购物车</div>
           <div class="addctr" v-show='foods.count'>
            <div class="dec" @click="dec(foods)">
                <i class="icon iconfont icon-jianhao"></i>
            </div> 
             <span class="num">{{foods.count}}</span>
            <div class="add" @click="add(foods)">
                 <i class="icon iconfont icon-jiahao"></i>
             </div>  
            </div>
        </div>
        <div class="item-two" v-if="foods.info">
          <p class="ft1">商品介绍</p>
          <p class="ft2">{{foods.info}}</p>
        </div>
        <div class="item-three">
          
          <div class="penglun">
            <div class="chilpen" v-for="(item,i) in foods.ratings" :key="i">
              <p class="foodtime">{{item.rateTime|date}}</p>
              <img :src="item.avatar" class="foodimgs"><span class="fooduser">{{item.username}}</span>
               <p class="foodtext">{{item.text}}</p>
            </div>
          </div>
        </div>
    
   </div>

  </div>
</template>

<script>
import {mapGetters} from 'vuex';
import BScroll from 'better-scroll';
import Vue from 'vue';
import gouwuche from '../gouwuche/gouwuche.vue';
import selectRatings from '../selectRatings/selectRatings.vue';
import {formatDate} from '../ratings/date';
export default {
  components:{
   'v-gouCar':gouwuche,
   'v-selectctr':selectRatings
  },
  filters:{
    date(time){
      let date=new Date(time)
      return formatDate(date,'yyyy-MM-dd hh:mm');
    }
  },
  computed:{
    ...mapGetters([
      'showfood',
      'foods',
      'selectRatings'
    ])
  },
  watch:{
    foods(){
      this.$nextTick(()=>{
        setTimeout( () => {
          this.showpages()
        },300)
      })
    }
  },
  methods:{
    showpages(){
      let showpage=new BScroll(this.$refs.foodpag,{
        click:true
      })
    },
    addcar(foods){
      Vue.set(this.foods,'count',1)
      console.log(this.foods.count)
    },
    fanhui(){
    this.$store.dispatch('fanPage')
    },
    add(a){
     a.count++
    },
    dec(d){
    if(this.foods.count==0){
      return 0
    }
    d.count--
    }
   
  }
  
}
</script>

<style lang="less" scoped>
.foodinfo{
    position:fixed;
    top:0;
    left: 0;
    right: 0;
    bottom:0;
    background-color:#f3f5f7;
    overflow: hidden;
    .bd{
       width:100%;
      .foodimg{
        img{
          width:100%;  
        }
      }
        .item-one{
          width:100%;
          margin-bottom: 0.36rem;
          background-color:white;
          border-bottom:0.05rem solid rgba(7,17,27,0.1);
          .addce{
            position:relative;
           bottom:1rem;
            left:5.1rem;
            width:1.48rem;
            height: 0.48rem;
            font-size:0.28rem;
            padding-left:0.3rem;
            padding-right:0.24rem;
            padding-top:0.18rem;
            color:rgb(255,255,255);
            background-color:rgb(0,160,220);
            border-radius:0.5rem;
          }
            .addctr{
            position:relative;
            bottom:0.9rem;
            left:3rem;
            width:4rem;
            height:0.48rem;
            text-align:center;
            display: flex;
            justify-content: flex-end;
            .dec{
            color:rgb(0,160,220);
            .icon{
                font-size:0.48rem;
            }
        }
             .num{
            display:block;
            font-size:0.3rem;
            bottom:0.75rem;
            width:0.6rem;
            padding-left:0.1rem;
            padding-top:0.1rem;
       
        }
        .add{
           
            color:rgb(0,160,220);
             .icon{
                font-size:0.48rem;
            }
        }         

          }
        .fn1{
          padding-top:0.36rem;
          margin-left: 0.36rem;
          font-size:0.38rem;
          font-weight:800;
          color:rgb(7,17,27);
          }
        .fn2{
          margin-top:0.3rem;
          margin-left: 0.36rem;
          font-size:0.24rem;
          color:rgb(147,153,159);
        span{
          margin-right:0.24rem;
          }
        }
        .fn3{
          margin-bottom:0.36rem;
          margin-top:0.36rem;          
          margin-left: 0.36rem;
          font-size:0.45rem;
          color:rgb(240,20,20);
          font-weight:800;
        .fft{
          font-size:0.24rem;
          font-weight:normal;
          margin-right:0.04rem;
        }
      }
     
    }
        .item-two{
          width:100%;
          height:3rem;
          margin-bottom: 0.36rem;
          background-color:white;
          border-bottom:0.05rem solid rgba(7,17,27,0.1);
          .ft1{
          position: relative;
          top:0.36rem;
          left: 0.36rem;
          font-size:0.38rem;
          font-weight:600;
          color:rgb(7,17,27);
        }
        .ft2{
          margin-top:0.57rem;
          margin-left: 0.36rem;
          padding-right:0.5rem;
          line-height:0.48rem;
          font-size:0.3rem;
          color:rgb(147,153,159);
        }
        }
       

        .item-three{
          background-color:white;
          .fs1{
          position: absolute;
          padding-top:0.36rem;
          left:0.36rem;
          font-size:0.38rem;
          font-weight:800;
          color:rgb(7,17,27);
          }
          .fs2{
            display: flex;
            flex:row;
            padding-left:0.36rem;
           .fs{
            margin-top:1.1rem;
            margin-right:0.16rem;
            padding-left:0.24rem;
            padding-right:0.24rem;
            padding-top:0.16rem;
            padding-bottom:0.16rem;
            font-size:0.3rem;
            font-weight: 700rem;
            color:rgb(147,153,159);
            }
            .s1{
              background-color:rgb(0,160,220);
              color:rgb(255,255,255);
              border-radius:0.05rem;
              span{
                font-size:0.24rem;
                padding-left:0.05rem;
              }
            }
             .s2{
              background-color:rgba(0,160,220,0.2);
              color:rgb(77,85,93);
              border-radius:0.05rem;
              span{
                font-size:0.24rem;
                padding-left:0.05rem;
              }
            }
            .s3{
              background-color:rgba(77,85,93,0.2);
              color:rgb(77,85,93);
              border-radius:0.05rem;
              span{
                font-size:0.24rem;
                padding-left:0.05rem;
              }
            }
          }
          .fs3{
            width:100%;
            padding-left:0.36rem;
            padding-top:0.36rem;
            padding-bottom:0.36rem;
            font-size:0.3rem;
            color:rgb(147,153,159);
            border-bottom:0.02rem solid rgba(7,17,27,0.2);
          }
          .penglun{
            width:100%;
            background-color:white;
            .chilpen{
              padding-left:0.36rem;
              padding-top:0.32rem;
              padding-bottom:0.32rem;
              margin-right:0.36rem;
              // margin-bottom:0.4rem;
              border-bottom:0.02rem solid rgba(7,17,27,0.2);
              .foodimgs{
                position: relative;
                right:-95%;
                bottom:0.3rem;
                width:0.4rem;
                height:0.4rem;
                border-radius:0.2rem;
              }
              .foodtime{
                font-size:0.24rem;
                font-weight: 400;
                color:rgb(147,153,159);
              }
              .fooduser{
                font-size:0.24rem;
                font-weight: 400;
                color:rgb(147,153,159);
                position: relative;
                right:-70%;
                bottom:0.4rem;
              }
              .foodtext{ 
                // padding-left:0.08rem;
                font-size:0.28rem;
                font-weight:400;
                line-height:0.36rem;
                color:rgb(7,17,27);
              }
            }
          }
        }
     
    }
}
</style>


