<template>
  <div id="app">
  <v-header></v-header>
  <v-moutop></v-moutop>
  <div class="tab">
    <div class="tab-item">
      <router-link to="/goods">商品</router-link>
    </div>
     <div class="tab-item">
      <router-link to="/ratings">评价</router-link>
    </div>
     <div class="tab-item">
      <router-link to="/seller">商家</router-link>
    </div>
  </div>
  <keep-alive>
  <router-view></router-view>
  </keep-alive>
   <v-footer></v-footer>
  </div>

</template>

<script>
import header from './components/header/header.vue'; 
import moutop from './components/moutop/moutop.vue';
import footer from './components/footer/footer.vue'
export default {
  components:{
    'v-header':header,
    'v-moutop':moutop,
    'v-footer':footer

  },
   created(){
    this.$store.dispatch('getSeller');
  }
}
</script>

<style lang="less" scoped>
#app{
  margin:0;
  padding:0;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  .tab{
    width:100%;
    height:0.8rem;
    line-height:0.8rem;
    font-size:0.28rem;
    color:rgb(77,85,93);
    display:flex;
    .tab-item{
      border-bottom: 0.05rem solid rgba(7,17,27,0.1);
      flex:1;
      padding-left:0.96rem;
    a{
      display: block;
      }

    }
  }
  .active{
    color:rgb(0,160,220);
  }
 
}
body{
  margin:0;
}
</style>



