<template>
  <div class="goods">
     <div class="side-item" ref="sort">
       <div>
         <div class="foodsort" v-for="(item,i) in goods" :key="i" @click="selectMenu(i)" :class="{'currt':i===currh}" >
           <span class="text">{{item.name}}</span>
        </div>
       </div>
     </div>
     <div class="goods-item" ref="menusf">
      <div>
         <div v-for="food in goods" :key="food.type" class="chilheight" >
            <div class="toptip">{{food.name}}</div>
          <div class="foodprod" v-for="(foods,k) in food.foods" :key="k" >
            <img :src="foods.icon" @click="showfoods(foods)">
            <ul class="foodmenu">
              <li class="fo1">{{foods.name}}</li>
              <li class="fo2" >{{foods.description}}</li>
              <li class="fo3">月销售{{foods.sellCount}}份<span  class="haoz">好评率{{foods.rating}}％</span></li>
              <li class="fo4"><span class="mony">¥</span>{{foods.price}}</li>
              <v-gouwu :foods="foods"></v-gouwu>
            </ul>
          </div>
       </div>
      </div>
     </div>
     <foodinfo></foodinfo>
     <!-- <v-foof ></v-foof> -->
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import BScroll from 'better-scroll';
import foodinfo from '../foodinfo/foodinfo.vue';
import gouwuche from '../gouwuche/gouwuche.vue';
// import footer from '../footer/footer.vue';




export default {
data:function(){
return {
  listHeigh:[],
  scrollY:0,
  showfoodinfo:false
}
},
components:{
  foodinfo,
  "v-gouwu":gouwuche,
  // "v-foof":footer

},
computed:{
  ...mapGetters([
    'goods'
    ]),
  currh(){
  
    for(let i=0;i<this.listHeigh.length;i++){
      let height1 = this.listHeigh[i];
      let height2 = this.listHeigh[i+1];
      if(this.scrollY>=this.listHeigh[i]&&this.scrollY<this.listHeigh[i+1]){
        return i;
      }
    }
    return 0;
                
  }
},
 created(){
   this.$store.dispatch('getGoods')
 },
 watch:{
  goods(){
   this.$nextTick(()=>{
     this.sortsc();
     this.mainmenu();
     this.calcheight()
   })
  }

 },
 methods:{
   showfoods(foods){
   this.$store.dispatch('showfoods',foods);
   this.showfoodinfo=true;
   },
  // better-scroll
   sortsc(){
     let sortscc=new BScroll(this.$refs.sort,{
       click:true
     });
   },
   mainmenu(){
     this.mainuff=new BScroll(this.$refs.menusf,{
       click:true,
       probeType:3
     });
     this.mainuff.on('scroll',(pro)=>{
       this.scrollY=Math.abs( Math.round(pro.y))+2
     })
    
   },
   calcheight(){
     let chilList = this.$refs.menusf.getElementsByClassName("chilheight");
     let height=0;
     this.listHeigh.push(height);
     for(let i=0;i<chilList.length;i++){
       height+=chilList[i].clientHeight;
       this.listHeigh.push(height);
     }
   },
   selectMenu(i){
				let chilList = this.$refs.menusf.getElementsByClassName('chilheight');
				let el = chilList[i];
			  this.mainuff.scrollToElement( el , 200 );
			}
 }
}
</script>

<style lang="less" scoped>
.goods{
  position: absolute;
  top:3.79rem;
  bottom:0.9rem;
  width:100%;
  display:flex;
  overflow: hidden;
  .side-item{
  width:1.8rem;
  flex:0 0 1.6rem;
  font-size:0.26rem;
  color:rgba(77,85,93,0.7);
  overflow: hidden;
 
    .foodsort{ 
    background-color:#f3f5f7;
    height:1.4rem;
    padding-left:0.24rem;
    padding-right:0.24rem;
    display: table;
    &.currt{
       color:white;
      background-color: rgb(0,160,220);;
    }
    .text{
      width:1.12rem;
      line-height:0.4rem;
      display:table-cell;
      vertical-align: middle;
      border-bottom:0.02rem solid rgba(7,17,27,0.1);
    }
    }
  }
  .goods-item{
    flex:1;
    .toptip{
      font-size:0.24rem;
      height: 0.58rem;
      line-height:0.58rem;
      background-color:#f3f5f7;
      border-left: 0.06rem solid #d9dde1;
      color:rgb(147,153,159);
      padding-left:0.32rem;
    }
    .foodprod{
      padding-left:0.36rem;
      padding-top:0.36rem;
      padding-bottom:0.36rem;
      border-bottom:0.02rem solid rgba(7,17,27,0.1);
     img{
       width:1.4rem;
       height:1.4rem;
     }
     .foodmenu{
       display:inline-block;
       vertical-align: top;
       padding-top:0.04rem;
       padding-left:0.2rem;
       width:3.06rem;
       height:1.6rem;
       .fo1{
         font-size:0.28rem;
         font-weight: 0.4;
         color:rgb(7,17,27);
         line-height:0.28rem;
       }
       .fo2{
         padding-top:0.16rem;
         overflow: hidden;
         white-space:nowrap;
         text-overflow:ellipsis;
         font-size:0.2rem;
         color:rgb(147,153,159);
         line-height:0.2rem;
       }
       .fo3{
          padding-top:0.16rem;
          font-size:0.2rem;
          color:rgb(147,153,159);
          line-height:0.2rem;
          .haoz{
            padding-left:0.24rem;
          }
       }
       .fo4{
         padding-top:0.16rem;
         font-size:0.32rem;
         color:rgb(240,20,20);
         font-weight:700;
         line-height:0.48rem;
         .mony{
          font-size:0.2rem;
          font-weight:normal;
          padding-right:0.05rem;

         }
       }
     }
    }
  }
}
</style>




