<template>
	<div class="seller" ref="bdff">
    <div class="count" >
      <div>
      <div class="item-one">
        <div class="name">{{seller.name}}</div>
        <div class="stars">
          <div>
           <ul >
             <li v-for="star in foodstar" class="star" :key="star" :class="star"></li>
             <li>
                 <span class="seller">月售{{seller.sellCount}}单</span>
             </li>
           </ul>
          </div>
         
        </div>
        <div class="xin">
          <li><i class="icon iconfont icon-xin1" :class="{on:flag}" @click="flag=!flag"></i></li>
          <li v-if="!flag">收藏</li>
          <li v-if="flag">已收藏</li>
          </div>
      </div>
      <div class="item-two">
        <div class="f1">
          <li class="f">起送价</li>
          <li class="s">{{seller.minPrice}}<span>元</span></li>
        </div>
        <div class="f1">
          <li class="f">商家配送</li>
          <li class="s">{{seller.deliveryPrice}}<span>元</span></li>
         </div> 
        <div class="f1">
          <li class="f">平均配送时间</li>
          <li class="s">{{seller.deliveryTime}}<span>元</span></li>
        </div>
      </div>
      <div class="item-three">
        <div class="tip">
          公告与活动
          
        </div>
        <div class="jiesao">{{seller.bulletin}}</div>
      </div>
      <div class="item-four">
        <div class="imf">
      <img src="../../../static/imgs/decrease.png" >
      <img src="../../../static/imgs/discount.png" >
      <img src="../../../static/imgs/discount.png" >
      <img src="../../../static/imgs/special.png">
      <img src="../../../static/imgs/special.png"> 
        </div>
        <div class="imr">
          <p v-for="(sell,k) in seller.supports" :key="k">{{sell.description}}</p>
        </div>
      </div>

		<div class="pic-wrap" ref='menuWrap'>
			<ul class="" ref='picList'>
				<li v-for='item in seller.pics' :key="item" class="pic-item">
					<img :src="item" alt="">
				</li>
			</ul>
		</div>
    <div class="item-bot">
      <span>商家信息</span>
      <ul v-for="item in seller.infos" :key="item">
        <li>{{item}}</li>
      </ul>
    </div>
    </div>
    </div>
	</div>
</template>

<script>
	import { mapGetters } from 'vuex';
  import BScroll from 'better-scroll';
  import star from '../star/starsmall.vue';
 	export default {
     data(){
       return{
         flag:false
       }
     },
 		computed:{
 			...mapGetters([
 				'seller'
       ]),
       foodstar(){
      let sever=[];
      let inta=Math.floor(this.seller.foodScore);
      let decu=this.seller.foodScore-inta
      for(let i=0;i<inta;i++){
        sever.push("ALL")
      }
      if(decu>0.5){
        sever.push("HELF")
      }
      while(sever.length<5){
        for(let i=sever.length;i<5;i++){
          sever.push("NO")
        }
      }
      return sever;
  }
     },
     components:{
       star
     },
 		watch:{
 			seller(){
        
 				let picWidth = 120;
	      let margin = 6;
	      let width = (picWidth + margin) * this.seller.pics.length - margin;
	      this.$refs.picList.style.width = width + 'px';

 				if( !this.picBScroll ){
	 				this.picBScroll = new BScroll( this.$refs.menuWrap , {
	 					scrollX: true,
	 				})
	 			}else{
	 				this.picBScroll.refresh();
	 			}
 			}
     },
     methods:{
        scroll(){
          let de=new BScroll(this.$refs.bdff,{
          click:true
        })
        }
     },
    created(){
      this.$nextTick(()=>{
        setTimeout(()=> {
          this.scroll()
        }, 30);
      })
    }
     
	}

</script>

<style scoped lang="less">
	
	.seller{
    height: 9rem;
    display:fixed;
    top:0;
    left: 0;
    right:0;
    overflow:hidden;
  
    .count{
      width:100%;
      .item-one{
        background-color:white;
        border-bottom:0.02rem solid rgba(77,85,93,0.4);
        .name{
          font-size:0.35rem;
          font-weight:500;
          padding-top:0.56rem;
          margin-left:0.36rem;
          line-height:0.3rem;
        }
        .stars{
          margin-left:0.36rem;
          margin-top:0.36rem;
          margin-bottom:0.5rem;
          ul li{
            display:inline-block;
          }   
          .star{
              margin-right:0.14rem;
              width:0.4rem;
              height:0.4rem;
              color:rgb(255,153,0);
              &.ALL{
                background-image: url(../star/star_on.png);
                background-size:0.4rem 0.4rem;
                background-repeat: no-repeat;
            }
            &.NO{
                background-image: url(../star/star_off.png);
                background-size:0.45rem 0.45rem;
                background-repeat: no-repeat;
            }
            &.HELF{
                background-image: url(../star/star_half.png);
                background-size:0.4rem 0.4rem;
                background-repeat: no-repeat;
            }
        }
          .seller{
            top:0.2rem;
            font-size:0.24rem;
            font-weight:400;
            background-color:white;
            color:rgb(77,85,93);
            margin-left:0.2rem;
          }
        }
        .xin{
           position: absolute;
            top:0.6rem;
            right:0.53rem;
            font-size:0.24rem;
            text-align:center;
            line-height:0.3rem;
          .icon{
            font-size:0.6rem;
            color:rgb(77,85,93);
            &.on{
              color:red;
            }
          }
        }
      }
      .item-two{
        display: flex;
         background-color:white;
        .f1{
          width:5.5rem;
          padding-left:0.36rem;
          padding-right:0.36rem;
          flex:1;
          padding-top:0.36rem;
          text-align:center;
          .f{
            margin-bottom:0.23rem;
            font-size:0.24rem;
            color:rgb(147,153,159);
          }
          .s{
            margin-bottom:0.4rem;
            font-weight: 500;
            color:rgb(7,17,27);
            font-size:0.4rem;
            span{
              font-size:0.28rem;
              margin-left:0.1rem;
            }
          }
        
          
        }
      }
      .item-three{
        background-color:white;
        margin-bottom:0.32rem;
        margin-top:0.36rem;
        padding-left:0.36rem;
        padding-right:0.36rem;
        .tip{
          padding-top:0.36rem;
          font-size:0.3rem;
          margin-bottom:0.2rem;
          font-weight:500;
        }
        .jiesao{
          font-size:0.24rem;
          line-height:0.48rem;
          color:rgb(240,20,20);

        }
      }
      .item-four{
        background-color:white;
        margin-top:0.5rem;
        padding-top:0.36rem;
        padding-left:0.36rem;
        padding-left:0.36rem;
        display: flex;
        .imf{
          flex:0,0,0.5rem;
          width:0.5rem;
          img{
            width:0.4rem;
            height: 0.4rem;
            margin-bottom:0.3rem;
          }
        }
        .imr{
          flex:1;
          p{
          font-size:0.24rem;
          font-weight: 400;
          margin-bottom:0.53rem;
          padding-left:0.4rem;
          }
        }
      }
      	.pic-wrap{
			padding: 18px;
			width: 100%;
			overflow: hidden;
			box-sizing: border-box;
			white-space: nowrap;
			.pic-item{
				display: inline-block;
				margin-right: 0.06rem;
				width: 2.4rem;
				height: 1.8rem;
				img{
					width: 100%;
					height: 100%;
				}
				&:last-child{
					margin: 0;
				}
			}
    }
    .item-bot{
        background-color:white;
        padding-top:0.36rem;
         padding-left:0.36rem;
         padding-right:0.36rem;
       span{
         padding-top:0.36rem;
         font-size:0.3rem;
         font-weight: 500;
       }
       ul li{
         margin-top:0.24rem;
         font-size:0.24rem;
         line-height:0.3rem;
         padding-bottom:0.24rem;
         padding-top:0.24rem;
        border-bottom:0.03rem solid #f3f5f7;

       }
    }
    }
	}


</style>
	


