<template>
  <div class="penctr">
    <div class="item-three" ref="bd">
        <div class="fs2">
            <div class="fs s1" @click="selectsort(2)">{{selectTypes[0]}}<span>{{ratings.length}}</span></div>
            <div class="fs s2" @click="selectsort(0)">{{selectTypes[1]}}<span>{{GoodTypes}}</span></div>
            <div class="fs s3" @click="selectsort(1)">{{selectTypes[2]}}<span>{{BadTypes}}</span></div>
        </div>
        <div class="fs3" @click="toggle" :class="{fft:toggletype}">
          <i class="icon iconfont icon-duihao" :class="{on:toggletype}"></i>
          只看有内容的评价
        </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';


export default {
  computed:{
    ...mapGetters([
      'selectTypes',
      'ratings',
      'selectRatings',
      'toggletype'
    ]),
    GoodTypes(){
     return this.ratings.filter( good=>{
      return good.rateType === 0
      }).length;
    },
    BadTypes(){
      return this.ratings.filter(rating=>{
       return rating.rateType===1
      }).length;
    }
  },
  created(){
    this.$store.dispatch('selectTypes',['全部','推荐','吐槽']);
  },
 
  methods:{
    selectsort(e){
     this.$store.dispatch('changeCount',e)
    },
    toggle(){
      this.$store.dispatch('toggle')
    }

  }
  
}
</script>
<style lang="less" scoped>
    .penctr{
        .item-three{
          width:100%;
          background-color:white;
          .fs2{
            display: flex;
            flex:row;
            padding-left:0.36rem;
           .fs{
             text-align:center;
             width:1.1rem;
             height:0.65rem;
            line-height:0.65rem;
            margin-top:0.36rem;
            margin-bottom:0.36rem;
            margin-right:0.16rem;
            padding-left:0.24rem;
            padding-right:0.24rem;
            font-size:0.3rem;
            font-weight: 600; 
            }
            .s1{
              background-color:rgb(0,160,220);
              color:rgb(255,255,255);
              border-radius:0.1rem;
              span{
                font-size:0.2rem;
                padding-left:0.1rem;
              }
            }
             .s2{
              background-color:rgba(0,160,220,0.2);
              color:rgb(77,85,93);
              border-radius:0.1rem;
              span{
                font-size:0.2rem;
                padding-left:0.1rem;
              }
            }
            .s3{
              background-color:rgba(77,85,93,0.2);
              color:rgb(77,85,93);
              border-radius:0.1rem;
              span{
                font-size:0.24rem;
                padding-left:0.1rem;
              }
            }
          }
          
          .fs3{
            width:100%;
            padding-left:0.36rem;
            padding-top:0.36rem;
            padding-bottom:0.36rem;
            font-size:0.3rem;
            color:rgb(147,153,159);
            border:0.02rem solid rgba(7,17,27,0.2);
            &.fft{
              color:black;
            }
            .icon{
                margin-right:0.14rem;
                 &.on{
              color:green;
            }
            }
           
          }

        }
    }
</style>



