<template>
  <div class="ratings" ref="scoi">
   <div class="itemmain" >
    <div class="toprat">
      <div class="left-rat">
          <div class="lf1">{{seller.score}}</div>
          <div class="lf2">综合评分</div>
          <div class="lf3">高于周边商家{{seller.rankRate}}%</div>
      </div>
      <div class="right-rat">
        <div class="r1">
          <div class="f1">
            服务态度:
            <ul>
              <li v-for="star in severstart" :key="star" class="serve" :class="star"></li>
               <li class="serve">{{seller.serviceScore}}</li>
            </ul>
          </div>
          <div class="f1">
            商品评分:
            <ul>
           <li v-for="fots in  foodstar" :key="fots" class="foodscr" :class="fots"></li>
           <li class="foodscr">{{seller.foodScore}}</li>
         </ul>
          </div>
          <div class="f1">
            送达时间:
            <p class="time">{{seller.deliveryTime}}分钟送达</p>
          </div>
        </div>
      </div>
    </div>
     <selectRatings></selectRatings>
      <div class="penlun">
        <div class="children" v-for="(rating,i) in selectRatings" :key="i">
          <div class="userimg">
          <img :src="rating.avatar" >
          </div>
            <div class="user">
              <div class="name"><span>{{rating.username}}</span><span class="time">{{rating.rateTime|rundate}}</span></div>
              <ul class="sttr">
                <li class="star">
                  <star :score="rating.score"></star>
                </li>
                <li class="delivetime">{{rating.deliveryTime}}送达</li>
              </ul>
              <div class="text">{{rating.text}}</div>
              <div class="foods">
                <ul>
                   <li><i class="icon iconfont icon-down" v-if="rating.rateType==1" :class="{down:rating.rateType==1}"></i></li>
                   <li> <i class="icon iconfont icon-damuzhion" v-if="rating.rateType==0" :class="{up:rating.rateType==0}"></i></li>
                  <li v-for="food in rating.recommend" :key="food" class="fo">{{food}}</li>
                </ul>
              </div>
               </div>
          </div>
        </div>
   </div>
  </div>
</template>

<script>
import {mapGetters} from 'vuex';
import BScroll from 'better-scroll';
import selectRatings from '../selectRatings/selectRatings.vue';
import {formatDate} from './date.js';
import star from '../star/starsmall'
export default {

  created(){
   this.$store.dispatch('GetRatings')
  },
  components:{
   selectRatings,
   star

  },
   filters:{
   rundate(value){
    let date= new Date(value);
    return formatDate(date,'yyyy-MM-dd hh:mm');
   }
  },
  computed:{
    ...mapGetters([
      'ratings',
      'seller',
     'selectTypes',
      'selectRatings'
    ]),
    severstart(){
      let sever=[];
      let inta=Math.floor(this.seller.serviceScore);
      let decu=this.seller.serviceScore-inta
      for(let i=0;i<inta;i++){
        sever.push("ALL")
      }
      if(decu>0.5){
        sever.push("HELF")
      }
      while(sever.length<5){
        for(let i=sever.length;i<5;i++){
          sever.push("NO")
        }
      }
      return sever;

    },
    foodstar(){
      let sever=[];
      let inta=Math.floor(this.seller.foodScore);
      let decu=this.seller.foodScore-inta
      for(let i=0;i<inta;i++){
        sever.push("ALL")
      }
      if(decu>0.5){
        sever.push("HELF")
      }
      while(sever.length<5){
        for(let i=sever.length;i<5;i++){
          sever.push("NO")
        }
      }
      return sever;
  }
  },
  watch:{
   selectRatings(){
     this.$nextTick(()=>{
       if( !this.ecBScroll ){
	 				this.ecBScroll = new BScroll( this.$refs.scoi , {
	 					click: true,
	 				})
	 			}else{
	 				this.ecBScroll.refresh();
	 			}
     })
   }
 }
}
</script>

<style lang="less" scoped>
  .ratings{
    position: absolute;
    top:3.8rem;
    bottom:0;
    left: 0;
    width:100%;
    overflow: hidden;
    .itemmain{
      background-color:#f3f5f7;
    .toprat{
      display:flex;
      margin-bottom:0.4rem;
      background-color:rgb(255,255,255);
      .left-rat{
        flex:0 0 2.6rem;
        width:2.6rem;
        text-align: center;
        padding-top:0.36rem;
        padding-bottom:0.36rem;
        .lf1{
          font-size:0.6rem;
          color:rgb(255,153,0);
          font-weight:500;
          margin-bottom:0.21rem;
        }
        .lf2{
          font-size:0.3rem;
         font-weight:500;
          line-height:0.24rem;
          color:rgb(7,17,27);
          margin-top:0.16rem;
        }
        .lf3{
          font-size:0.2rem;
          color:rgba(7,17,27,0.5);
          margin-top:0.22rem;
          line-height:0.24rem;
        }
      }
      .right-rat{
        flex:1;
        width:100%;
        // height: 2rem;
        padding-top:0.36rem;
         padding-left:0.36rem;
         border-left:0.05rem solid #f3f5f7;
        .r1{
          .f1{
            display:flex;
            font-size:0.24rem;
            font-weight: 500;
            color:rgb(7,17,27);
            margin-bottom:0.13rem;
           
            .serve{
              display:inline-block;
              margin-left:0.16rem;
              font-size:0.24rem;
              font-weight:700;
              width:0.34rem;
              height:0.34rem;
               color:rgb(255,153,0);
              &.ALL{
                background-image: url(../star/star_on.png);
                background-size:0.34rem 0.34rem;
                background-repeat: no-repeat;
            }
            &.NO{
                background-image: url(../star/star_off.png);
                background-size:0.34rem 0.34rem;
                background-repeat: no-repeat;
            }
            &.HELF{
                background-image: url(../star/star_half.png);
                background-size:0.34rem 0.34rem;
                background-repeat: no-repeat;
            }
            }
            .foodscr{
              display:inline-block;
              font-size:0.24rem;
              font-weight:700;
              margin-left:0.16rem;
              width:0.34rem;
              height:0.34rem;
              color:rgb(255,153,0);
               &.ALL{
                background-image: url(../star/star_on.png);
                background-size:0.34rem 0.34rem;
                background-repeat: no-repeat;
            }
            &.NO{
                background-image: url(../star/star_off.png);
                background-size:0.34rem 0.34rem;
                background-repeat: no-repeat;
            }
            &.HELF{
                background-image: url(../star/star_half.png);
                background-size:0.34rem 0.34rem;
                background-repeat: no-repeat;
            }
            }
            .time{
              padding-left:0.24rem;
              font-size:0.2rem;
               color:rgba(7,17,27,0.5);

            }
            
          }
         
        }
      }

    }
          .penlun{
            background-color:white;
            padding:0;
            .children{
            border-bottom:0.01rem solid rgba(7,17,27,0.3);
            margin-left:0.36rem;
            margin-right:0.36rem;
            padding-top:0.36rem;
            display:flex;
            .userimg{
            flex:0,0,0.3;
            width:0.3rem;
            img{
              width:0.6rem;
              height:0.6rem;
              border-radius:0.6rem;
            }
          }
          .user{
            flex:1;
            position:relative;
            margin-left:0.8rem;
            margin-right:0.36rem;
            font-size:0.28rem;
            .name{
              margin-bottom:0.08rem;
              width:400;
              color:rgb(7,17,27);
              line-height:0.24rem;
              .time{
              color:rgb(147,153,159);
              line-height:0.24rem;
              position:absolute;
              right:-0.3rem;
            }
            }
            .sttr li{
              margin-bottom:0.2rem;
              display:inline-block;
              margin-top:0.12rem;
              margin-right:0.12rem;
              font-size:0.24rem;
              color:rgb(147,153,159);
              line-height:0.24rem;

            }
          }
          .text{
            width:100%;
            font-size:0.28rem;
            line-height:0.36rem;
            margin-bottom:0.2rem;
          }
          .foods{
            i{
              margin-top:0.16rem;
              font-size:0.3rem;
              position: absolute;
              top:0.8rem;
              left:-0.4rem;
             
              &.up{
                color:rgb(0,160,220);
              }
              &.down{
                color:gray;
              }
            }
            li{
              font-size:0.24rem;
              display:inline-block;
              margin-right:0.16rem;
              margin-bottom:0.16rem;
              text-align:center;
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
              

            }
            .fo{
              border:0.01rem solid rgba(147,153,159,0.3);
              width:0.9rem;
              height:0.42rem;
              line-height:0.42rem;
              color:rgb(147,153,159);

            }

          }
        }
            }
   
  }
}
</style>




