import types from '../types';
import axios from 'axios';


var state={
goods:[],
foods:{},
showfood:false
}

var getters={
goods(state){
    return state.goods
},
foods(state){
    return state.foods
},
showfood(state){
    return state.showfood
}
}

var actions={
getGoods({commit,state}){
axios.get('./data.json').then((res)=>{
   
 commit(types.GET_GOODS,res.data.goods)
    
})
},
showfoods({commit,state},k){
    commit('showFoods',k)
},
fanPage({commit,state}){
   commit('fanFft')
}
}

var mutations={
    [types.GET_GOODS](state,data){
      state.goods=data
    },
    showFoods(state,k){
      state.foods=k;
      state.showfood=true;
    },
    fanFft(state){
        state.showfood=false;
    }

}

export default{
    state,
    getters,
    actions,
    mutations
}


