import types from '../types.js';
import axios from 'axios';
var state = {
	seller : {},
	moutop:false
}

var getters = {
	seller( state ){
		return state.seller;
	},
	moutop( state ){
		return state.moutop;
	}
}

var actions = {

	getSeller( { commit,state } ){
		axios.get('./data.json').then( (res) => {		
			commit( types.GET_SELLER , res.data.seller);
		});
	},
	showmoutop({ commit }){
		commit(types.showMoutop);
	},
	hidemoutop({ commit }){
		commit(types.hideMoutop)
	}
}

var mutations ={ 

	[types.GET_SELLER]( state , data ){
		state.seller = data;
	},
	[types.showMoutop](state){
		state.moutop=true;
	},
	[types.hideMoutop](state){
		state.moutop=false;
	}
}

export default {
	state,
	getters,
	actions,
	mutations
}


