
import types from '../types.js'
import axios from 'axios';

var state = {
	ratings:[],
	selectTypes:[],
	selectRatings:[],
	startTypes:2,
	toggletype:false
}

var getters = {
	ratings(state){
		return state.ratings
	},
	selectTypes(state){
		return state.selectTypes
	},
	selectRatings(state){
		return state.selectRatings
	},
	toggletype(state){
		return state.toggletype
	}
}

var actions = {
	GetRatings({commit,state}){
		axios.get('./data.json').then((res)=>{
			
		commit(types.GET_RATINGS,res.data.ratings)
		
		})
	},
	selectTypes({commit,state},_types){
		commit (types.SELECTTYPES,_types)
	},
	changeCount({commit},e){
		commit (types.CHANGECOUNTS,e)
	},
	toggle({commit,state}){
		commit( types.TOGGLE )
	}

}

var mutations = { 
	[types.GET_RATINGS](state,data){
	state.ratings=data;
	state.selectRatings=state.ratings;
},
[types.SELECTTYPES](state,types){
	state.selectTypes=types;
},
[types.CHANGECOUNTS](state,e){
	state.startTypes=e;
	state.selectRatings=state.ratings.filter(rating=>{
		if(state.startTypes===2){
			return true;
		}else{
			return rating.rateType==state.startTypes
		}
		}
	)
},
[types.TOGGLE](state){
 state.toggletype=!state.toggletype;
 state.selectRatings=state.ratings.filter(rating=>{
	 if(state.toggletype&&!rating.text){
		 return false;
	 }else if(state.startTypes===2){
		 return true;
	 }else{
		 return rating.rateType===state.startTypes
	 }
 })
 console.log(state.selectRatings)

}	
}

export default {
	state,
	getters,
	actions,
	mutations
}


