<template>
  <transition enter-active-class="animated bounceInLeft" leave-active-class="animated bounceOutRight">
    <div class="moutop" v-show="moutop">
    <p>{{seller.name}}</p>
    <star :score="seller.score"></star>
    <div class="centercomm">
      <hr class="hr1">
      <span class="fn1">优惠信息</span>
      <hr class="hr2">
      <div v-for="(item,k) in seller.supports" :key="k" class="guangg">
        <span class="ccer">{{item.description}}</span>
      </div>
      <img src="../../../static/imgs/decrease.png" class="imgg1">
      <img src="../../../static/imgs/discount.png" class="imgg2">
      <img src="../../../static/imgs/discount.png" class="imgg3">
      <img src="../../../static/imgs/special.png" class="imgg4">
      <img src="../../../static/imgs/special.png" class="imgg5">
      <hr class="hr3">
      <span class="fn7">商家公告</span>
      <hr class="hr4">
      <p class="shtip">{{seller.bulletin}}</p>
    </div>
     <div class="fanhui"  @click="hidemoutop">
        <i class="icon iconfont icon-X"></i>
      </div>
    </div>
  </transition>
</template>

<script>
import { mapGetters } from 'vuex';
import animate from 'animate.css';
import star from '../star/star.vue';


export default {
  components:{
			star
		},
  computed:{
    ...mapGetters([
      'seller',
      'moutop'
    ])
  },
  methods:{
    hidemoutop(){
     this.$store.dispatch('hidemoutop')
    }
  }
}
</script>

<style lang="less" scoped>
  .moutop{
    z-index:9;
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
    background-color:rgba(7,17,27,0.8);
    p{
      padding-left:2.36rem;
      padding-top:1.28rem;
      font-size:0.38rem;
      font-weight:700;
      color:rgb(255,255,255);
      line-height:0.32rem;
    }
    .centercomm{
      .hr1{
        position: absolute;
        top:2.79rem;
        left:0.72rem;
        width:2.24rem;
        color:rgba(255,255,255,0.5);
      }
      .hr2{
        position: absolute;
        top:2.79rem;
       right:0.72rem;
        width:2.24rem;
        color:rgba(255,255,255,0.5);
      }
      .fn1{
        position: absolute;
        top:2.79rem;
        left: 3.1rem;
        font-size:0.32rem;
        font-weight:700;
        color:rgb(255,255,255);
      }
      .guangg{
        position:relative;
        top:1.14rem;
        left:1.52rem;
        width:100%;
        height:0.24rem;
        margin-bottom:0.35rem;
        font-size:0.24rem;
        font-weight:200;
        color:rgb(255,255,255);
        line-height:0.56rem;
        .ccer{
          display:block;
          
        }
      }

      .hr3{
        position: absolute;
        top:7rem;
        left:0.72rem;
        width:2.24rem;
        color:rgba(255,255,255,0.5);
      }
      .hr4{
        position: absolute;
        top:7rem;
        right:0.72rem;
        width:2.24rem;
        color:rgba(255,255,255,0.5);
      }
      .fn7{
        position: absolute;
        top:7rem;
        left: 3.1rem;
        font-size:0.32rem;
        font-weight:700;
        color:rgb(255,255,255);
      }
      .shtip{
        width:5.82rem;
        position:absolute;
        top:6.55rem;
        right:0.73rem;
        font-size:0.24rem;
        font-weight:200;
        color:rgb(255,255,255);
        line-height:0.48rem;


      }
      img{
        width:0.35rem;
        height:0.35rem;
      }
      .imgg1{
        position: absolute;
        top:3.65rem;
        left:0.93rem;
      }
      .imgg5{
        position: absolute;
        top:6rem;
        left:0.93rem;
      }
      .imgg2{
        position: absolute;
        top:4.23rem;
        left:0.93rem;
      }
      .imgg3{
        position: absolute;
        top:4.81rem;
        left:0.93rem;
      }
      .imgg4{
        position: absolute;
        top:5.42rem;
        left:0.93rem;
      }

    }
    
  .fanhui{
     color:rgba(255,255,255,0.5);
     position: absolute;
     bottom:0.85rem;
     left:3.4rem;
     
    .iconfont{
      font-size:0.64rem;
     
      
    }
  }

  }
</style>





